// eslint-disable-next-line @typescript-eslint/no-explicit-any
type KeyAccessor<T extends string> = <O extends Record<T, any>, R = O extends Record<T, infer U> ? U : never>(object: O) => R;
export function pluck<T extends string>(...keys: T[]): KeyAccessor<T> {
  // @ts-expect-error dont have time for keys
  return (obj) =>  keys.reduce((r, k) => r && r[k], obj);
}

export const getGl = pluck('gl');
export const getCamera = pluck('camera');
