import { pluck } from "../pluck";
import { IGridCell, ColConstraint, GridConstraints, PositionedCell, CompleteGridCell } from "./IGridCell";
import { GridCol } from "./GridCol";

export type GridCellPosition<T = unknown> = PositionedCell<T>['rect'];

type GridCellUpdateSubscriber<T, R = void> = (rect: GridCellPosition<T>) => R;
type GlobalGridCellUpdateSubscriber<T, R = void> = (rect: GridCellPosition<T>, key?: T) => R;

type GridCellSUbscriptionPair<T> = [
  subs: Set<GridCellUpdateSubscriber<T>>,
  resolver: GridCellUpdateSubscriber<T, Promise<void[]>>,
];


const _subNOOP = (() => undefined) as GridCellUpdateSubscriber<unknown>;
export class Grid<T> {
  constructor(public constrain: GridConstraints, public constraintValue: ColConstraint = [1, -1]) { }
  cols: GridCol<T>[] = [];
  cells = new Map<T, IGridCell<T>>();

  push(cell: IGridCell<T>) {
    if (cell.col) {
      this.cols[cell.col.index].add(cell);
    } else {
      // find shortest col or create new
      this.findColForCell(cell).add(cell);
    }
    this.storeLocal(cell);
  }
  protected _needsResizing = false;
  protected storeLocal(cell: IGridCell<T>) {
    this.cells.set(cell.key, cell);
  }

  setContraint(newConstraint: ColConstraint) {
    this.constraintValue = newConstraint;
    this._needsResizing = true;
  }

  #subscribers = new Map<T, GridCellSUbscriptionPair<T>>();
  private getOrMakeSubscriberSet(cellKey: T): GridCellSUbscriptionPair<T> {
    const subscriber = this.#subscribers.get(cellKey);
    if (subscriber) return subscriber;

    const newSub = [new Set(), (rect) => {
      const prom = Promise.resolve(rect);
      const [subs] = this.getOrMakeSubscriberSet(cellKey);
      return Promise.all(Array.from<GridCellUpdateSubscriber<T>>(subs).map((resolver) => prom.then(resolver)));
    }] as GridCellSUbscriptionPair<T>;

    this.#subscribers.set(cellKey, newSub);

    return newSub;
  }
  toJSON() {
    const { constrain, constraintValue, cells, cols, isWaitingForResize } = this;
    const cellMap = new Map();
    for (const [k, v] of cells.entries()) {
      const { col: { heightSum, index } = {}, next: { key: next } = {}, ...cellData } = v;
      cellMap.set(k, { ...cellData, col: { heightSum, index }, next: next ? { key: next } : undefined });
    }
    return { constrain, constraintValue, cells: cellMap, cols, isWaitingForResize };
  }
  private getSubscriptionResolver(cellKey: T) {
    const resolver = this.#subscribers.get(cellKey);
    if (!resolver) return _subNOOP;
    return resolver[1];
  }

  #gloablSubscribers = new Set<GlobalGridCellUpdateSubscriber<T>>();
  subscribeGlobal(sub: GlobalGridCellUpdateSubscriber<T>) {
    this.#gloablSubscribers.add(sub);
    return () => void this.#gloablSubscribers.delete(sub);
  }
  subscribeToCell(cellKey: T) {
    const [subs] = this.getOrMakeSubscriberSet(cellKey);
    return (callback: GridCellUpdateSubscriber<T>) => {
      let cT: number | undefined;
      const throttledCallback = (rect: GridCellPosition<T>) => {
        cT = requestAnimationFrame(() => {
          cT = undefined;
          callback(rect);
        });
      };
      subs.add(throttledCallback);
      const unsubscribe = () => {
        subs.delete(throttledCallback);
        cT && cancelAnimationFrame(cT);
      };
      return unsubscribe;
    };
  }


  notifyCellSubscribers(cellKey: T, rect: GridCellPosition) {
    this.getSubscriptionResolver(cellKey)(rect);
    this.#gloablSubscribers.forEach(sub => sub(rect, cellKey));
  }
  _toMatrixCache?: PositionedCell<T>[];
  toMatrix(): PositionedCell<T>[][] {
    if (!this.isCached()) this.resize();
    const maxHeight = this.getMaxHeight();
    let x = 0;
    const result = this.cols.map(col => {
      let y = 0;
      const { heightSum } = col;
      const width = heightSum / maxHeight;
      const colArr = [...col].map(cell => {
        const height = cell.height / heightSum;
        const rect: PositionedCell<T>['rect'] = [x, y, width, height];
        this.notifyCellSubscribers(cell.key, rect);
        y += height;

        return {
          column: col.index,
          rect,
          cell: { ...cell, col: col.index },
        };
      });
      x += width;

      return colArr;
    });
    return result;
  }

  remove(cellKey: T) {
    const cell = this.cells.get(cellKey) as CompleteGridCell<T>;
    if (!cell) return;
    const { col } = cell;
    col && col.remove(cell);
    this.clearCell(cell);
    delete this._toMatrixCache;
    this._needsResizing = true;
  }

  clearCell(cellOrCellKey: IGridCell<T> | T) {
    const ck: T = (cellOrCellKey as IGridCell<T>).key ?? (cellOrCellKey as T);
    const cell = this.cells.get(ck) as CompleteGridCell<T>;
    if (!cell) return;
    this.cells.delete(ck);
    this.notifyCellSubscribers(ck, [1, 1, 0, 0]);
  }
  isCached(): this is { _toMatrixCache: PositionedCell<T>[][] } {
    return !this._needsResizing && !!this._toMatrixCache;
  }

  getCachedAndNotify(): PositionedCell<T>[] {
    if (!this.isCached()) return [];
    const result = this._toMatrixCache;
    const excluded = new Set(this.#subscribers.keys());
    for (let i = 0; i < result.length; i++) {
      const { cell: { key }, rect } = result[i];
      this.notifyCellSubscribers(key, rect);
      excluded.delete(key);
    }
    if (excluded.size) {
      this._needsResizing = true;
      // @ts-expect-error dht
      delete this._toMatrixCache;
    }
    requestAnimationFrame(() => {
      for (const key in excluded) {
        this.notifyCellSubscribers(key as unknown as T, [1, 1, 0, 0]);
      }
    });
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    console.log(`grid cache:${(this as any).id || ''}:`, this.length, result.length, excluded.size, performance?.now().toPrecision(2), !!this._toMatrixCache);
    return result;
  }
  toArray(forceUpdate = false) {
    if (!forceUpdate && this.isCached()) return this.getCachedAndNotify();
    const result = ([] as PositionedCell<T>[]).concat(...this.toMatrix());
    this._toMatrixCache = result;
    return result;
  }

  get length() {
    return this.cells.size;
  }

  get isWaitingForResize() {
    return this._needsResizing;
  }

  resize() {
    for (const col of this.cols) {
      GridCol.clearCol(col);
    }
    this.cols = this.cols.filter(pluck('heightSum'));
    const cells = Array.from(this.cells.values());
    this.cells.clear();
    cells.forEach(cell => this.push(cell));
    this._needsResizing = false;
  }

  private _makeCol() {
    const col = new GridCol<T>(this.cols.length);
    this.cols.push(col);
    return col;
  }

  private _findOrCreateCol(predicate: (value: GridCol<T>, index: number, obj: GridCol<T>[]) => boolean, needsSort = false) {
    return (needsSort ? this.sortedCols() : this.cols).find(predicate) ?? this._makeCol();
  }

  private sortedCols() {
    return this.cols.sort((a, b) => a.heightSum - b.heightSum);
  }

  findColForCell({ height }: IGridCell<T>): GridCol<T> {
    switch (this.constrain) {
    case GridConstraints.COL_WIDTH: {
      // only return col if new resultant width > minWidth, max value doesn't matter
      const maxHeight = this.getMaxHeight(height);
      const [minColWidth] = this.constraintValue;
      return this._findOrCreateCol(({ heightSum }) => ((heightSum + height) / maxHeight) <= minColWidth, true);
    }
    case GridConstraints.COL_HEIGHT: {
      // return col if new height < max
      const [maxColHeight] = this.constraintValue;
      return this._findOrCreateCol(({ heightSum }) => (heightSum + height) <= maxColHeight, true);
    }
    case GridConstraints.MAX_COLS: {
      // return shortest col
      const [maxCols] = this.constraintValue;
      const { length } = this.cols;
      return this._findOrCreateCol(() => !!length && length >= maxCols, length >= maxCols);
      // if () {
      //   return this._makeCol();
      // }
      // let [min] = this.cols;
      // for (let i = 0; i < this.cols.length; i++) {
      //   const { [i]: current } = this.cols;
      //   min = min && current.heightSum > min.heightSum ? min : current;
      // }
      // return min;
    }
    case GridConstraints.MIN_CELL_HEIGHT: {
      // return col where resultant cell.height / cell.colHeight > value
      const [minHeight] = this.constraintValue;
      return this._findOrCreateCol((col) => {
        const newHeight = col.heightSum + height;
        return Array.from(col).every((cel) => (cel.height / newHeight) >= minHeight);
      });
    }
    }
  }

  private getMaxHeight(height = 0) {
    return this.cols.reduce((sum, { heightSum }) => sum + heightSum, height);
  }
}
