import { useBufferHeight, useRefFrame } from '@anonymous.club/ui';
import { useMemo, useRef } from 'react';
import type {
  Mesh,
  MeshStandardMaterial,
} from 'three';
import {
  MirroredRepeatWrapping,
} from 'three/src/constants';
import { EllipseCurve } from 'three/src/extras/curves/EllipseCurve';
import { Color } from 'three/src/math/Color';
import { lerp } from 'three/src/math/MathUtils';
import { PI } from '../constants';

export function WorldBackDrop() {
  const ref = useRef<Mesh>();
  const buffer = useBufferHeight();
  const inc = 10;
  const NUM_POINTS = 64;
  const points = useMemo(() => {
    const bigR = buffer.diag * 2 + inc;
    return new EllipseCurve(buffer.diag * 0.75, 0, bigR, bigR, PI._1_2, PI._3_2, true, 0).getPoints(NUM_POINTS);
  }, [buffer.diag, inc]);
  useRefFrame(([mesh], [{ camera, clock, performance }]) => {
    mesh.rotation.copy(camera.rotation);
    const mat = mesh.material as MeshStandardMaterial;
    const t = clock.getElapsedTime();
    mat.bumpScale = lerp(
      Math.sin(t * 0.5) * 0.3 + 0.05,
      mat.bumpScale,
      1 - performance.current,
    );
    mat.displacementBias = Math.sin(t * 0.3) * 100;
  }, [ref]);
  return (
    <mesh
      castShadow
      receiveShadow
      layers={0}
      ref={ref}
      // scale={[1.01, 1.01, 1.01]}
      >
      <latheGeometry args={[points, NUM_POINTS, PI._1_2, PI._1]} />
      <meshStandardMaterial
        // flatShading
        // transparent
        // wireframe
        // depthTest={false}
        attach="material"
        bumpScale={0.1}
        color={new Color(0xffffff)}
        displacementBias={0}
        displacementScale={-250}
        emissive={new Color(0xcebbbb).setHSL(0.1, 0.02, 1)}
        emissiveIntensity={0.9}
        fog={true}
        metalness={0.5}
        // metalness={0.05}
        roughness={0.01}
        // side={DoubleSide}
        >
        <noiseTexture
          args={[16, 64]}
          // attach="map"
          attach="displacementMap"
          repeat-x={1}
          repeat-y={2}
          rotation={0}
          wrapS={MirroredRepeatWrapping}
          wrapT={MirroredRepeatWrapping}
          />
        <noiseTexture
          args={[1024, 1]}
          // attach="map"
          attach="bumpMap"
          repeat-x={256}
          repeat-y={64}
          // rotation={PI._1_4}
          wrapS={MirroredRepeatWrapping}
          wrapT={MirroredRepeatWrapping}
          />
        {/* <noiseTexture
          args={[4, 4]}
          // attach="map"
          attach="map"
          repeat-x={60}
          repeat-y={60}
          rotation={PI._1_4}
          wrapS={MirroredRepeatWrapping}
          wrapT={MirroredRepeatWrapping}
          /> */}
      </meshStandardMaterial>
    </mesh>
  );
}
