import { inverseLerp, lerp } from "three/src/math/MathUtils";
import { PI } from "../constants";

export const verticalSlice = (divisions = 1, gutter = 0) => {
  const dStart = PI._3_4;
  const dDelta = PI._1_2 / divisions;
  return (cell: number) => {
    return [0, 1].map((i) => dStart + dDelta * (cell + i) - (i * gutter));
  };
};
enum SliceModes {
  POINT_SCALAR = 0,
  COORDS,
}
type SliceMode = keyof typeof SliceModes;
export const vSlice = (mStart: keyof typeof PI = '_3_4', mLength: keyof typeof PI = '_1_2', mode: SliceMode = 'COORDS') => {
  const startAngle = PI[mStart];
  const angleSlope = PI[mLength];
  switch (SliceModes[mode]) {
  case SliceModes.POINT_SCALAR: {
    return (maxHeight = 1, gutter = 0) => {
      const iLF: (i: number) => number = maxHeight === 1 ? i => i : i => inverseLerp(0, maxHeight, i);
      return (start: number, end: number): Readonly<[number, number]> => [
        startAngle + lerp(0, angleSlope, iLF(start)),
        lerp(0, angleSlope, iLF(end)) - gutter,
      ];
    };
  }
  case SliceModes.COORDS: {
    return (maxHeight = 1, gutter = 0) => {
      const m = angleSlope / maxHeight;
      const gutteredStartAngle = startAngle - gutter;
      return (start: number, end: number): Readonly<[number, number]> => [
        startAngle + m * start,
        gutteredStartAngle + m * end,
      ];
    };
  }
  }
};
export type Slicer = ReturnType<ReturnType<typeof vSlice>>;
// console.log(PI._2);
console.log(
  vSlice()(10, 0.5)(0, 10),
  vSlice('_0', '_2', 'POINT_SCALAR')(1, 0.02)(0, 1),
  inverseLerp(PI._0, PI._2, 1),
  lerp(PI._0, PI._2, 1),
  verticalSlice(1, 0.5)(0),
);
