const BLANK_IMG = new ImageData(2, 2);
BLANK_IMG.data.set([
  255, 255, 255, 255,
  0, 0, 0, 255,
  255, 255, 255, 255,
  0, 0, 0, 255,
]);
type ImageMaterialProps = {
  src: string;
  onLoad?(image: ImageDimensions): void;
  delay?: number;
};
// @ts-expect-error dht
const dataCache: Record<string, ImageData> = window['@@imgData'] ?? {};
// @ts-expect-error dht
if (window && !window['@@imgData']) window['@@imgData'] = dataCache;

dataCache['BLANK'] = BLANK_IMG;

const DOWNSAMPLING = 1 / 1;
type ImageLoadTextureProps = {
  tRef: React.MutableRefObject<CanvasTexture | undefined>;
  imgR: React.MutableRefObject<CanvasRenderingContext2D | null>;
  iLoader: React.MutableRefObject<HTMLImageElement>;
  onLoad: NonNullable<ImageMaterialProps['onLoad']>;
} & ImageMaterialProps;
function ImageLoadTexture({ src, onLoad, delay, tRef, imgR, iLoader }: ImageLoadTextureProps) {
  const [hasImage, setHasImage] = useState(!!dataCache[src]);
  useRefLayoutEffect(([ct]) => {
    const iD = dataCache[src];
    if (iD) {
      ct.image = iD;
      ct.needsUpdate = true;
    }
  }, [tRef], [src]);
  useRefLayoutEffect(([context]) => {
    let delOpen: NodeJS.Timeout;
    let isMounted = true;
    const iD = dataCache[src];
    if (iD) {
      if (tRef.current) tRef.current.image = iD;
      setHasImage(true);
      const { width, height } =iD;
      delOpen = setTimeout(() => onLoad({ width, height }));
      return () => {
        isMounted = false;
        clearTimeout(delOpen);
      };
    }
    // const context = imgEl.getContext && imgEl.getContext('2d');
    // if (!context) return;
    const iht = iLoader.current;
    iht.onload = () => {
      const { naturalHeight, naturalWidth } = iht;
      const h = naturalHeight * DOWNSAMPLING;
      const w = naturalWidth * DOWNSAMPLING;
      context.canvas.width = w;
      context.canvas.height = h;
      context.fillStyle = '#020202';
      context.fillRect(0, 0, w, h);
      context.drawImage(iht, 0, 0, w, h);
      const i = context.getImageData(0, 0, w, h);
      // onLoad && onLoad(i);

      dataCache[src] = i;
      if (!isMounted) return;
      if (tRef.current) tRef.current.image = i;
      // setImgKey(src);
      // const { width, height } = i;
      delOpen = setTimeout(() => onLoad({ width: w, height: h }));
      setHasImage(true);
      ;
    };
    iht.src = src;
    return () => {
      isMounted = false;
      iht.onload = null;
      clearTimeout(delOpen);
    };
  }, [imgR], [src]);
  if (!hasImage) return null;
  return (
    <canvasTexture
      flipY
      anisotropy={2}
      attach={"map"}
      // attach={dataCache[src] ? "map" : undefined}
      // generateMipmaps={false}
      image={dataCache[src]}
      magFilter={LinearFilter}
      // minFilter={NearestMipmapLinearFilter}
      minFilter={LinearFilter}
      needsUpdate={false}
      ref={tRef}
      />
  );
}
