import create from 'zustand';
import { pluck } from '../utils/pluck';

type ImgArrayStore = {
  imgs: string[];
  removedSet: Set<string>;
  renderSet: Set<string>;
  length: number;
  setImgArray(imgs: string[]): void;
};

export const useImgArray = create<ImgArrayStore>((set, get) => ({
  imgs: [],
  length: 0,
  removedSet: new Set<string>(),
  renderSet: new Set<string>(),
  setImgArray(imgs) {
    const { imgs: old, removedSet: imgSet, renderSet } = get();
    renderSet.clear();
    const newSet = new Set(imgs);
    const notInNewSet = old.filter(i => {
      const result = newSet.has(i);
      if (result) imgSet.delete(i);
      else imgSet.add(i);
      return !result;
    });
    newSet.forEach(s => renderSet.add(s));
    // console.log(...notInNewSet, newSet, imgSet);
    set({ imgs, length: imgs.length });
  },
}));

const _pluckImgs = pluck('imgs');
const _pluckRemoved = pluck('removedSet');
const _pluckRender = pluck('renderSet');
const _pluckImgSet = pluck('setImgArray');
const _pluckImgSetLength = pluck('imgs', 'length');

export const useImgArrayImgs = () => useImgArray(_pluckImgs);
export const useImgArrayRemovals = () => useImgArray(_pluckRemoved);
export const useImgArrayRender = () => useImgArray(_pluckRender);
export const useImgArraySetter = () => useImgArray(_pluckImgSet);
export const useImgArrayImgsLength = () => useImgArray(_pluckImgSetLength);
